/**
 * 
 */
export class TextStyle {
    /***/
    font: string;
    /***/
    fontSize: number;
    /***/
    color: string;
    /***/
    bold: boolean;
    /**
     * 
     * @default false
     */
    italic: boolean;

    /***/
    underline: boolean;
    /***/
    underlineColor: string;

    /** */
    strikethrough = false;
    /**  */
    strikethroughColor:string = null;

    /**
     * <p></p>
     * @default  "left"
     */
    align: string;

    /**
     * <p></p>
     * @default  "top"
     */
    valign: string;

    /**
     * top,middle,bottom
     */
    alignItems: string;

    /**
     * <p></p>
     */
    leading: number;

    /**
     * <p></p>
     * 0
     * @default 0
     */
    stroke: number;
    /**
     * <p></p>
     * @default "#000000";
     */
    strokeColor: string;

    constructor() {
        this.font = "";
        this.fontSize = 12;
        this.color = "#000000";
        this.bold = false;
        this.italic = false;
        this.underline = false;
        this.underlineColor = null;
        this.align = "left";
        this.valign = "top";
        this.alignItems = "middle";
        this.leading = 2;
        this.stroke = 0;
        this.strokeColor = "#000000";
    }
}

