/**
 * <code>ShaderDefine</code> 
 */
export class ShaderDefine {

	/**
	 * @internal
	 *  texture uniform gamma define
	 */
	static _texGammaDefine: { [key: string]: ShaderDefine } = {};

	/**@internal */
	_index: number;
	/**@internal */
	_value: number;
	/**
	 * 
	 * @param index 
	 * @param value 
	 */
	constructor(index: number, value: number) {
		this._index = index;
		this._value = value;
	}
}






