import { Pool } from "../utils/Pool";
import { Point } from "./Point";

/**
 * <p><code>Rectangle</code>  (x, y) </p>
 * <p>Rectangle  xywidth  height </p>
 */
export class Rectangle {

    /**@private x=0,y=0,width=0,height=0*/
    static EMPTY: Rectangle = new Rectangle();
    /***/
    static TEMP: Rectangle = new Rectangle();

    /** @private */
    private static _temB: number[] = [];
    /** @private */
    private static _temA: number[] = [];

    /**  X */
    x: number;
    /**  Y */
    y: number;
    /** */
    width: number;
    /** */
    height: number;

    /**
     *  <code>Rectangle</code> 
     * @param	x  X 
     * @param	y  Y 
     * @param	width 
     * @param	height 
     */
    constructor(x: number = 0, y: number = 0, width: number = 0, height: number = 0) {
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
    }

    /**  X  x  width */
    get right(): number {
        return this.x + this.width;
    }

    /**  Y y  height */
    get bottom(): number {
        return this.y + this.height;
    }

    /**
     *  Rectangle 
     * @param	x	x  X 
     * @param	y	x  Y 
     * @param	width	
     * @param	height	
     * @return	
     */
    setTo(x: number, y: number, width: number, height: number): Rectangle {
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
        return this;
    }

    /**
     * 
     */
    reset(): Rectangle {
        this.x = this.y = this.width = this.height = 0;
        return this;
    }

    /**
     * 
     */
    recover(): void {
        if (this == Rectangle.TEMP || this == Rectangle.EMPTY) {
            //console.log("recover Temp or Empty:", this);
            return;
        }
        Pool.recover("Rectangle", this.reset());
    }

    /**
     * 
     */
    static create(): Rectangle {
        return Pool.getItemByClass("Rectangle", Rectangle);
    }

    /**
     *  source 
     * @param	sourceRect	 Rectangle 
     * @return	
     */
    copyFrom(source: Rectangle): Rectangle {
        this.x = source.x;
        this.y = source.y;
        this.width = source.width;
        this.height = source.height;
        return this;
    }

    /**
     *  Rectangle 
     * @param x	 X 
     * @param y	 Y 
     * @return	 Rectangle  true false
     */
    contains(x: number, y: number): boolean {
        if (this.width <= 0 || this.height <= 0) return false;

        if (x >= this.x && x < this.right) {
            if (y >= this.y && y < this.bottom) {
                return true;
            }
        }
        return false;
    }

    /**
     *  rect  Rectangle  Rectangle  xywidth  height  Rectangle 
     * @param	rect Rectangle 
     * @return	 true  false
     */
    intersects(rect: Rectangle): boolean {
        return !(rect.x > (this.x + this.width) || (rect.x + rect.width) < this.x || rect.y > (this.y + this.height) || (rect.y + rect.height) < this.y);
    }

    /**
     *  rect  Rectangle  Rectangle  Rectangle null
     * @param rect	
     * @param out	
     * @return	
     */
    intersection(rect: Rectangle, out: Rectangle | null = null): Rectangle | null {
        if (!this.intersects(rect)) return null;
        out || (out = new Rectangle());
        out.x = Math.max(this.x, rect.x);
        out.y = Math.max(this.y, rect.y);
        out.width = Math.min(this.right, rect.right) - out.x;
        out.height = Math.min(this.bottom, rect.bottom) - out.y;
        return out;
    }

    /**
     * <p> Rectangle </p>
     * <p>union()  0 var rect2:Rectangle = new Rectangle(300,300,50,0);</p>
     * @param	 Rectangle  Rectangle 
     * @param	out	Rectangle.TEMP
     * @return	 Rectangle 
     */
    union(source: Rectangle, out: Rectangle | null = null): Rectangle {
        out || (out = new Rectangle());
        this.clone(out);
        if (source.width <= 0 || source.height <= 0) return out;
        out.addPoint(source.x, source.y);
        out.addPoint(source.right, source.bottom);
        return this;
    }

    /**
     *  Rectangle  xywidth  height  Rectangle 
     * @param out	Rectangle.TEMP
     * @return Rectangle  xywidth  height  Rectangle 
     */
    clone(out: Rectangle | null = null): Rectangle {
        out || (out = new Rectangle());
        out.x = this.x;
        out.y = this.y;
        out.width = this.width;
        out.height = this.height;
        return out;
    }

    /**
     *  Rectangle  x  y  width  height 
     */
    toString(): string {
        return this.x + "," + this.y + "," + this.width + "," + this.height;
    }

    /**
     *  Rectangle  Rectangle  xywidthheight 
     * @param	rect  Rectangle 
     * @return	 true , false
     */
    equals(rect: Rectangle): boolean {
        if (!rect || rect.x !== this.x || rect.y !== this.y || rect.width !== this.width || rect.height !== this.height) return false;
        return true;
    }

    /**
     * <p></p>
     * <p></p>
     * @param x	 X 
     * @param y	 Y 
     * @return  Rectangle 
     */
    addPoint(x: number, y: number): Rectangle {
        this.x > x && (this.width += this.x - x, this.x = x);//
        this.y > y && (this.height += this.y - y, this.y = y);//
        if (this.width < x - this.x) this.width = x - this.x;//
        if (this.height < y - this.y) this.height = y - this.y;//
        return this;
    }

    /**
     * @internal
     * 
     * @return 
     */
    _getBoundPoints() {
        var rst = Rectangle._temB;
        rst.length = 0;
        if (this.width == 0 || this.height == 0) return rst;
        rst.push(this.x, this.y, this.x + this.width, this.y, this.x, this.y + this.height, this.x + this.width, this.y + this.height);
        return rst;
    }

    /**
     * @internal
     * 
     */
    static _getBoundPointS(x: number, y: number, width: number, height: number, sp?: { width: number, height?: number }): number[] {
        var rst: any[] = Rectangle._temA;
        rst.length = 0;
        if (width == 0 || height == 0) return rst;
        if (sp) {
            x *= sp.width;
            y *= sp.height;
            width *= sp.width;
            height *= sp.height;
        }
        rst.push(x, y, x + width, y, x, y + height, x + width, y + height);
        return rst;
    }

    /**
     * @internal
     * 
     * @param pointList 
     * @return 
     */
    static _getWrapRec(pointList: ArrayLike<number>, rst: Rectangle | null = null): Rectangle {

        if (!pointList || pointList.length < 1) return rst ? rst.setTo(0, 0, 0, 0) : Rectangle.TEMP.setTo(0, 0, 0, 0);
        rst = rst ? rst : Rectangle.create();
        var i: number, len: number = pointList.length, minX: number, maxX: number, minY: number, maxY: number, tPoint: Point = Point.TEMP;
        minX = minY = 99999;
        maxX = maxY = -minX;
        for (i = 0; i < len; i += 2) {
            tPoint.x = pointList[i];
            tPoint.y = pointList[i + 1];
            minX = minX < tPoint.x ? minX : tPoint.x;
            minY = minY < tPoint.y ? minY : tPoint.y;
            maxX = maxX > tPoint.x ? maxX : tPoint.x;
            maxY = maxY > tPoint.y ? maxY : tPoint.y;
        }
        return rst.setTo(minX, minY, maxX - minX, maxY - minY);
    }

    /**
     *  Rectangle 
     * @return  Rectangle  0 true  false
     */
    isEmpty(): boolean {
        if (this.width <= 0 || this.height <= 0) return true;
        return false;
    }
}