import { Point } from "./Point";
import { Pool } from "../utils/Pool"

/**
 * <p> <code>Matrix</code> </p>
 * <p> Matrix  Matrix  Transform  matrix  Transform  transform x  y </p>
 */
export class Matrix {

    /**@private  <code>Matrix</code> */
    static EMPTY: Matrix = new Matrix();
    /** <code>Matrix</code> */
    static TEMP: Matrix = new Matrix();
    /**@internal */
    static _createFun: Function|null = null;

    /** x */
    a: number;
    /** y */
    b: number;
    /** x */
    c: number;
    /** y */
    d: number;
    /** x */
    tx: number;
    /** y */
    ty: number;
    /**@internal */
    _bTransform: boolean = false;

    /**
     *  <code>Matrix</code> 
     * @param a		 x 
     * @param b		 y 
     * @param c		 x 
     * @param d		 y 
     * @param tx	 x 
     * @param ty	 y 
     */
    constructor(a: number = 1, b: number = 0, c: number = 0, d: number = 1, tx: number = 0, ty: number = 0, nums: number = 0) {
        if (Matrix._createFun != null) {
            return Matrix._createFun(a, b, c, d, tx, ty, nums);
        }
        this.a = a;
        this.b = b;
        this.c = c;
        this.d = d;
        this.tx = tx;
        this.ty = ty;
        this._checkTransform();
    }

    /**
     * 
     * @return 
     */
    identity(): Matrix {
        this.a = this.d = 1;
        this.b = this.tx = this.ty = this.c = 0;
        this._bTransform = false;
        return this;
    }

    /**@internal */
    _checkTransform(): boolean {
        return this._bTransform = (this.a !== 1 || this.b !== 0 || this.c !== 0 || this.d !== 1);
    }

    /**
     *  x y 
     * @param	x  x 
     * @param	y  y 
     * @return	
     */
    setTranslate(x: number, y: number): Matrix {
        this.tx = x;
        this.ty = y;
        return this;
    }

    /**
     *  x  y  x  y 
     * @param	x  x 
     * @param	y  y 
     * @return 
     */
    translate(x: number, y: number): Matrix {
        this.tx += x;
        this.ty += y;
        return this;
    }

    /**
     * 
     * @param	x  x 
     * @param	y  y 
     * @return	
     */
    scale(x: number, y: number): Matrix {
        this.a *= x;
        this.d *= y;
        this.c *= x;
        this.b *= y;
        this.tx *= x;
        this.ty *= y;
        this._bTransform = true;
        return this;
    }

    /**
     *  Matrix 
     * @param	angle 
     * @return	
     */
    rotate(angle: number): Matrix {
        var cos: number = Math.cos(angle);
        var sin: number = Math.sin(angle);
        var a1: number = this.a;
        var c1: number = this.c;
        var tx1: number = this.tx;

        this.a = a1 * cos - this.b * sin;
        this.b = a1 * sin + this.b * cos;
        this.c = c1 * cos - this.d * sin;
        this.d = c1 * sin + this.d * cos;
        this.tx = tx1 * cos - this.ty * sin;
        this.ty = tx1 * sin + this.ty * cos;
        this._bTransform = true;
        return this;
    }

    /**
     *  Matrix 
     * @param	x  X  2D 
     * @param	y  Y  2D 
     * @return  Matrix 
     */
    skew(x: number, y: number): Matrix {
        var tanX: number = Math.tan(x);
        var tanY: number = Math.tan(y);
        var a1: number = this.a;
        var b1: number = this.b;
        this.a += tanY * this.c;
        this.b += tanY * this.d;
        this.c += tanX * a1;
        this.d += tanX * b1;
        return this;
    }

    /**
     * 
     * @param	out  Point 
     * @return	out
     */
    invertTransformPoint(out: Point): Point {
        var a1: number = this.a;
        var b1: number = this.b;
        var c1: number = this.c;
        var d1: number = this.d;
        var tx1: number = this.tx;
        var n: number = a1 * d1 - b1 * c1;

        var a2: number = d1 / n;
        var b2: number = -b1 / n;
        var c2: number = -c1 / n;
        var d2: number = a1 / n;
        var tx2: number = (c1 * this.ty - d1 * tx1) / n;
        var ty2: number = -(a1 * this.ty - b1 * tx1) / n;
        return out.setTo(a2 * out.x + c2 * out.y + tx2, b2 * out.x + d2 * out.y + ty2);
    }

    /**
     *  Matrix 
     * @param	out 
     * @return	out
     */
    transformPoint(out: Point): Point {
        return out.setTo(this.a * out.x + this.c * out.y + this.tx, this.b * out.x + this.d * out.y + this.ty);
    }

    /**
     *  Matrix txty
     * @param	out 
     * @return	out
     */
    transformPointN(out: Point): Point {
        return out.setTo(this.a * out.x + this.c * out.y /*+ tx*/, this.b * out.x + this.d * out.y /*+ ty*/);
    }

    /**
     *  X 
     * @return  X 
     */
    getScaleX(): number {
        return this.b === 0 ? this.a : Math.sqrt(this.a * this.a + this.b * this.b);
    }

    /**
     *  Y 
     * @return Y 
     */
    getScaleY(): number {
        return this.c === 0 ? this.d : Math.sqrt(this.c * this.c + this.d * this.d);
    }

    /**
     * 
     * @return 
     */
    invert(): Matrix {
        var a1: number = this.a;
        var b1: number = this.b;
        var c1: number = this.c;
        var d1: number = this.d;
        var tx1: number = this.tx;
        var n: number = a1 * d1 - b1 * c1;
        this.a = d1 / n;
        this.b = -b1 / n;
        this.c = -c1 / n;
        this.d = a1 / n;
        this.tx = (c1 * this.ty - d1 * tx1) / n;
        this.ty = -(a1 * this.ty - b1 * tx1) / n;
        return this;
    }

    /**
     *   Matrix 
     * @param	a  x 
     * @param	b  y 
     * @param	c  x 
     * @param	d  y 
     * @param	tx  x 
     * @param	ty  y 
     * @return 
     */
    setTo(a: number, b: number, c: number, d: number, tx: number, ty: number): Matrix {
        this.a = a, this.b = b, this.c = c, this.d = d, this.tx = tx, this.ty = ty;
        return this;
    }

    /**
     * 
     * @param	matrix 
     * @return	
     */
    concat(matrix: Matrix): Matrix {
        var a: number = this.a;
        var c: number = this.c;
        var tx: number = this.tx;
        this.a = a * matrix.a + this.b * matrix.c;
        this.b = a * matrix.b + this.b * matrix.d;
        this.c = c * matrix.a + this.d * matrix.c;
        this.d = c * matrix.b + this.d * matrix.d;
        this.tx = tx * matrix.a + this.ty * matrix.c + matrix.tx;
        this.ty = tx * matrix.b + this.ty * matrix.d + matrix.ty;
        return this;
    }

    /**
     * 
     * @param	m1 
     * @param	m2 
     * @param	out 
     * @return	 out
     */
    static mul(m1: Matrix, m2: Matrix, out: Matrix): Matrix {
        var aa: number = m1.a, ab: number = m1.b, ac: number = m1.c, ad: number = m1.d, atx: number = m1.tx, aty: number = m1.ty;
        var ba: number = m2.a, bb: number = m2.b, bc: number = m2.c, bd: number = m2.d, btx: number = m2.tx, bty: number = m2.ty;
        if (bb !== 0 || bc !== 0) {
            out.a = aa * ba + ab * bc;
            out.b = aa * bb + ab * bd;
            out.c = ac * ba + ad * bc;
            out.d = ac * bb + ad * bd;
            out.tx = ba * atx + bc * aty + btx;
            out.ty = bb * atx + bd * aty + bty;
        } else {
            out.a = aa * ba;
            out.b = ab * bd;
            out.c = ac * ba;
            out.d = ad * bd;
            out.tx = ba * atx + btx;
            out.ty = bd * aty + bty;
        }
        return out;
    }

    /**
     * 16
     * @param m1	
     * @param m2	
     * @param out	Array
     * @return  out
     */
    static mul16(m1: Matrix, m2: Matrix, out: any[]): any[] {
        var aa: number = m1.a, ab: number = m1.b, ac: number = m1.c, ad: number = m1.d, atx: number = m1.tx, aty: number = m1.ty;
        var ba: number = m2.a, bb: number = m2.b, bc: number = m2.c, bd: number = m2.d, btx: number = m2.tx, bty: number = m2.ty;
        if (bb !== 0 || bc !== 0) {
            out[0] = aa * ba + ab * bc;
            out[1] = aa * bb + ab * bd;
            out[4] = ac * ba + ad * bc;
            out[5] = ac * bb + ad * bd;
            out[12] = ba * atx + bc * aty + btx;
            out[13] = bb * atx + bd * aty + bty;
        } else {
            out[0] = aa * ba;
            out[1] = ab * bd;
            out[4] = ac * ba;
            out[5] = ad * bd;
            out[12] = ba * atx + btx;
            out[13] = bd * aty + bty;
        }
        return out;
    }

    /**
     * @private
     * 
     * @param	x  x 
     * @param	y  y 
     */
    scaleEx(x: number, y: number): void {
        var ba: number = this.a, bb: number = this.b, bc: number = this.c, bd: number = this.d;
        if (bb !== 0 || bc !== 0) {
            this.a = x * ba;
            this.b = x * bb;
            this.c = y * bc;
            this.d = y * bd;
        } else {
            this.a = x * ba;
            this.b = 0 * bd;
            this.c = 0 * ba;
            this.d = y * bd;
        }
        this._bTransform = true;
    }

    /**
     * @private
     *  Matrix 
     * @param	angle 
     */
    rotateEx(angle: number): void {
        var cos: number = Math.cos(angle);
        var sin: number = Math.sin(angle);
        var ba: number = this.a, bb: number = this.b, bc: number = this.c, bd: number = this.d;
        if (bb !== 0 || bc !== 0) {
            this.a = cos * ba + sin * bc;
            this.b = cos * bb + sin * bd;
            this.c = -sin * ba + cos * bc;
            this.d = -sin * bb + cos * bd;
        } else {
            this.a = cos * ba;
            this.b = sin * bd;
            this.c = -sin * ba;
            this.d = cos * bd;
        }
        this._bTransform = true;
    }

    /**
     *  Matrix 
     * @return  Matrix 
     */
    clone(): Matrix {
        var dec: Matrix = Matrix.create();
        dec.a = this.a;
        dec.b = this.b;
        dec.c = this.c;
        dec.d = this.d;
        dec.tx = this.tx;
        dec.ty = this.ty;
        dec._bTransform = this._bTransform;
        return dec;
    }

    /**
     *  Matrix  Matrix 
     * @param	dec  Matrix 
     * @return	 Matrix 
     */
    copyTo(dec: Matrix): Matrix {
        dec.a = this.a;
        dec.b = this.b;
        dec.c = this.c;
        dec.d = this.d;
        dec.tx = this.tx;
        dec.ty = this.ty;
        dec._bTransform = this._bTransform;
        return dec;
    }

    /**
     *  Matrix 
     * @return  Matrix abcdtx  ty
     */
    toString(): string {
        return this.a + "," + this.b + "," + this.c + "," + this.d + "," + this.tx + "," + this.ty;
    }

    /**
     * 
     */
    destroy(): void {
        this.recover();
    }

    /**
     * 
     */
    recover(): void {
        Pool.recover("Matrix", this.identity());
    }

    /**
     *  <code>Matrix</code> 
     * @return <code>Matrix</code> 
     */
    static create(): Matrix {
        return Pool.getItemByClass("Matrix", Matrix);
    }
}

