import { Context } from "../resource/Context";

/**
 * @private
 */
export class WordText {
    text: string;
    width: number;	//WordText-1
    pageChars: any[];	//texture
    pagecharsCtx: Context = null;  // ctxctxdrawToTexture
    scalex;	// 
    scaley;

    _nativeObj: any;
    _splitRender: boolean;	// 

    constructor() {
        if ((window as any).conch && !(window as any).conchConfig.conchWebGL)
            this._nativeObj = new (window as any)._conchWordText();
        else {
            this.width = -1;
            this.pageChars = [];
            this.scalex = 1;
            this.scaley = 1;
        }
    }

    setText(txt: string): void {
        this.text = txt;
        if (this._nativeObj)
            this._nativeObj._text = txt;
        else
            this.width = -1;
        this.cleanCache();
    }

    toString(): string {
        return this.text;
    }

    get length(): number {
        return this.text ? this.text.length : 0;
    }

    /**
     * 
     * textrender
     * TODO 
     */
    cleanCache(): void {
        if (this._nativeObj) {
            this._nativeObj.cleanCache();
            return;
        }

        // 
        //TODO 
        let chars = this.pageChars;
        if (chars.length > 0) {
            for( var i in chars){
                //should use for in since chars maybe sparse 
                let p = chars[i];
                if(!p)continue;
                let tex = p.tex;
                let words = p.words;
                if (words.length == 1 && tex && tex.ri) {// ri
                    tex.destroy();
                }
            }

            this.pageChars = [];
        }
        this.scalex = 1;
        this.scaley = 1;
    }

    get splitRender() {
        return this._splitRender;
    }

    set splitRender(value: boolean) {
        this._splitRender = value;
        if (this._nativeObj)
            this._nativeObj.splitRender = value;
    }
}