import { RenderInfo } from "../../renders/RenderInfo"
/**
 * TODO,
 */
export class CharRenderInfo {
    char: string = '';				// 
    tex: any;						//
    deleted: boolean = false; 	// 
    uv: any[] = new Array(8);// [0, 0, 1, 1];		//uv
    pos: number = 0;					//
    width: number;					//
    height: number; 				//
    bmpWidth: number;				//widthmargin
    bmpHeight: number;
    orix: number = 0;				// 
    oriy: number = 0;
    touchTick: number = 0;		//
    isSpace: boolean = false;		//width
    touch(): void {
        var curLoop: number = RenderInfo.loopCount;
        if (this.touchTick != curLoop) {// 
            this.tex.touchRect(this, curLoop);
        }
        this.touchTick = curLoop;
    }
}

