import { LayaEnv } from "../../LayaEnv";
import { AssetDb } from "../resource/AssetDb";
import { Utils } from "../utils/Utils";

/**
 * <p><code>URL</code> URLURL</p>
 * <p>formatURLURL</p>
 * <p>basePath</p>
 * <p>customFormatURL</p>
 */
export class URL {
    /**URL{"aaa/bb.png":"edcba","aaa/bb.png":"1342a"}formatURL"aaa/bb-1342a.png"*/
    static version: Record<string, string> = {};

    /** basePath+URL*/
    static basePath: string = "";
    /**{"aa/":"http://abc.com/"},aa/http://abc.com/*/
    static basePaths: Record<string, string> = {};

    /**root'~'url*/
    static rootPath: string = "";

    /**@private */
    private _url: string;
    /**@private */
    private _path: string;

    private static overrideFileExts: Record<string, string> = {};
    private static hasExtOverrides: boolean;

    static __init__() {
        //xiaomi location
        //Vivo location.protocol""
        // location.protocolundefined
        URL.rootPath = URL.basePath = (location && location.protocol != undefined && location.protocol != "") ? URL.getPath(location.protocol + "//" + location.host + location.pathname) : "";
    }

    static initMiniGameExtensionOverrides() {
        if (LayaEnv.isPreview)
            return;

        URL.overrideExtension(["rendertexture", "videotexture"], "rt.json");
        URL.overrideExtension(["controller"], "controller.json");
        URL.overrideExtension(["mc"], "mc.bin");
        URL.overrideExtension(["mcc"], "mcc.json");
        URL.overrideExtension(["shader"], "shader.json");
        URL.overrideExtension(["scene3d", "scene", "taa", "prefab"], "json");
        URL.overrideExtension(["fui"], "fui.json");
        URL.overrideExtension(["glsl"], "glsl.txt");
        URL.overrideExtension(["skel"], "skel.bin");
        URL.overrideExtension(["lavm"], "lavm.json");
    }

    /** <code>URL</code> */
    constructor(url: string) {
        this._url = URL.formatURL(url);
        this._path = URL.getPath(url);
    }

    /***/
    get url(): string {
        return this._url;
    }

    /***/
    get path(): string {
        return this._path;
    }

    /** URL customFormat = function(url:String):String{} */
    static customFormat: Function = function (url: string): string {
        return url;
    }

    /**
     * normalizedURLbasebaseURL.basePathURL.rootPath
     * @param url 
     * @param base URL.basePathURL.rootPath
     * @return 
     */
    static formatURL(url: string, base?: string): string {
        if (!url)
            return base || URL.basePath || "";

        if (url.startsWith("res://")) {
            let uuid = url.substring(6);
            let url2 = AssetDb.inst.UUID_to_URL(uuid);
            if (!url2)
                return url;

            url = url2;
        }

        let char1 = url.charCodeAt(0);
        if (url.indexOf(":") == -1 && char1 !== 47) { //format
            //
            if (URL.customFormat != null)
                url = URL.customFormat(url);

            let ver = URL.version[url];
            if (ver != null) {
                let i = url.lastIndexOf(".");
                url = url.substring(0, i) + "-" + ver + url.substring(i);
            }

            if (char1 === 126) // ~
                url = URL.join(URL.rootPath, url.substring(2));
            else {
                if (base == null) {
                    base = URL.basePath;
                    for (let k in URL.basePaths) {
                        if (url.startsWith(k)) {
                            base = URL.basePaths[k];
                            break;
                        }
                    }
                }
                url = URL.join(base, url);
            }
        }

        return url;
    }

    /**
     * 
     * @param url 
     * @return 
     */
    static postFormatURL(url: string): string {
        if (URL.hasExtOverrides) {
            let extold = Utils.getFileExtension(url);
            let ext = URL.overrideFileExts[extold];
            if (ext != null)
                url = Utils.replaceFileExtension(url, ext);
        }

        return url;
    }

    /**
     * ...
     */
    static normalize(url: string): string {
        if (url.indexOf("./") == -1)
            return url;

        let parts = url.split("/");
        let len = parts.length;
        let i = 0;
        while (i < len) {
            if (parts[i] == ".") {
                parts.splice(i, 1);
                len--;
                continue;
            }
            else if (parts[i] == '..') {
                let index: number = i - 1;
                if (index >= 0 && parts[index] !== '..') {
                    parts.splice(index, 2);
                    len -= 2;
                    i--;
                    continue;
                }
            }

            i++;
        }
        parts.length = len;
        return parts.join('/');
    }

    static getResURLByUUID(url: string): string {
        if (url.length >= 36 && url.charCodeAt(8) === 45 && url.charCodeAt(13) === 45) //uuid xxxxxxxx-xxxx-...
            return "res://" + url;
        else
            return url;
    }

    /**
    * 
    * @param base
    * @param path
    */
    static join(base: string, path: string): string {
        if (!path) return "";

        //
        if (path.indexOf(":") > 0)
            return path;

        if (base) {
            let char1 = path.charCodeAt(0);
            if (char1 !== 126 && char1 !== 47) { // ~ /
                if (base.charCodeAt(base.length - 1) !== 47)
                    path = base + "/" + path;
                else
                    path = base + path;
            }
        }

        return URL.normalize(path);
    }

    /**
     *  URL 
     * <p><b></b>/</p>
     * @param url url
     * @return 
     */
    static getPath(url: string): string {
        var ofs: number = url.lastIndexOf('/');
        return ofs > 0 ? url.substring(0, ofs + 1) : "";
    }

    /**
     *  URL 
     * @param url 
     * @return 
     */
    static getFileName(url: string): string {
        var ofs: number = url.lastIndexOf('/');
        return ofs > 0 ? url.substring(ofs + 1) : url;
    }

    /**
     * URL
     * @param url
     * @return
     */
    static getURLVerion(url: string): string {
        var index: number = url.indexOf("?");
        return index >= 0 ? url.substring(index) : null;
    }

    /**
     * URL
     * @originalExts ["scene"]
     * @targetExt "json"
     */
    static overrideExtension(originalExts: Array<string>, targetExt: string) {
        for (let ext of originalExts)
            URL.overrideFileExts[ext] = targetExt;
        URL.hasExtOverrides = true;
    }
}