import { Matrix } from "../maths/Matrix"

/**
 * <p> <code>Byte</code> </p>
 * <p> <code>Byte</code> </p>
 */
export class Byte {

    /**
     * <p> CPU  <code>getSystemEndian</code> </p>
     * <p> <code>BIG_ENDIAN</code> <br/>
     * <code>LITTLE_ENDIAN</code> </p>
     */
    static BIG_ENDIAN: string = "bigEndian";
    /**
     * <p> CPU  <code>getSystemEndian</code> </p>
     * <p> <code>LITTLE_ENDIAN</code> <br/>
     * <code>BIG_ENDIAN</code> </p>
     */
    static LITTLE_ENDIAN: string = "littleEndian";
    /**@private */
    private static _sysEndian: string = null;
    /**@private */
    protected _xd_: boolean = true;
    /**@private */
    private _allocated_: number = 8;
    /**@private */
    protected _d_: any
    /**@private DataView*/
    protected _u8d_: any;
    /**@private */
    protected _pos_: number = 0;
    /**@private */
    protected _length: number = 0;

    /**
     * <p></p>
     * <p> CPU </p>
     * <p> <code>BIG_ENDIAN</code> <br/>
     * <code>LITTLE_ENDIAN</code> </p>
     * @return 
     */
    static getSystemEndian(): string {
        if (!Byte._sysEndian) {
            var buffer: any = new ArrayBuffer(2);
            new DataView(buffer).setInt16(0, 256, true);
            Byte._sysEndian = (new Int16Array(buffer))[0] === 256 ? Byte.LITTLE_ENDIAN : Byte.BIG_ENDIAN;
        }
        return Byte._sysEndian;
    }

    /**
     *  <code>Byte</code> 
     * @param	data	TypedArrayArrayBuffer null 
     */
    constructor(data: any = null) {
        if (data) {
            this._u8d_ = new Uint8Array(data);
            this._d_ = new DataView(this._u8d_.buffer);
            this._length = this._d_.byteLength;
        } else {
            this._resizeBuffer(this._allocated_);
        }
    }

    /**
     *  ArrayBuffer 
     */
    get buffer(): ArrayBuffer {
        var rstBuffer: ArrayBuffer = this._d_.buffer;
        if (rstBuffer.byteLength === this._length) return rstBuffer;
        return rstBuffer.slice(0, this._length);
    }

    /**
     * <p> <code>Byte</code> <code>BIG_ENDIAN</code>  <code>BIG_ENDIAN</code> </p>
     * <p> CPU  <code>getSystemEndian</code> </p>
     * <p> <code>BIG_ENDIAN</code> <br/>
     *  <code>LITTLE_ENDIAN</code> </p>
     */
    get endian(): string {
        return this._xd_ ? Byte.LITTLE_ENDIAN : Byte.BIG_ENDIAN;
    }

    set endian(value: string) {
        this._xd_ = (value === Byte.LITTLE_ENDIAN);
    }

    /**
     * <p> <code>Byte</code> </p>
     * <p></p>
     * <p>2</p>
     */
    set length(value: number) {
        if (this._allocated_ < value) this._resizeBuffer(this._allocated_ = Math.floor(Math.max(value, this._allocated_ * 2)));
        else if (this._allocated_ > value) this._resizeBuffer(this._allocated_ = value);
        this._length = value;
    }

    get length(): number {
        return this._length;
    }

    /**@private */
    private _resizeBuffer(len: number): void {
        try {
            var newByteView: any = new Uint8Array(len);
            if (this._u8d_ != null) {
                if (this._u8d_.length <= len) newByteView.set(this._u8d_);
                else newByteView.set(this._u8d_.subarray(0, len));
            }
            this._u8d_ = newByteView;
            this._d_ = new DataView(newByteView.buffer);
        } catch (err) {
            throw "Invalid typed array length:" + len;
        }
    }

    /**
     * @private
     * <p></p>
     * <p> <code>Uint16</code> </p>
     * @return 
     */
    getString(): string {
        return this.readString();
    }

    /**
     * <p></p>
     * <p> <code>Uint16</code> </p>
     * @return 
     */
    readString(): string {
        return this._rUTF(this.getUint16());
    }

    /**
     * @private
     * <p> <code>start</code>  <code>len</code>  <code>Float32Array</code> </p>
     * <p><b></b> Float32Array  JavaScript  HTML5 Float32Array (Float32Array.buffer) DataView  DataView </p>
     * @param	start	
     * @param	len		
     * @return   Float32Array 
     */
    getFloat32Array(start: number, len: number): any {
        return this.readFloat32Array(start, len);
    }

    /**
     *  <code>start</code>  <code>len</code>  <code>Float32Array</code> 
     * @param	start	
     * @param	len		
     * @return   Float32Array 
     */
    readFloat32Array(start: number, len: number): any {
        var end: number = start + len;
        end = (end > this._length) ? this._length : end;
        var v: any = new Float32Array(this._d_.buffer.slice(start, end));
        this._pos_ = end;
        return v;
    }

    /**
     * @private
     *  <code>start</code>  <code>len</code>  <code>Uint8Array</code> 
     * @param	start	
     * @param	len		
     * @return   Uint8Array 
     */
    getUint8Array(start: number, len: number): Uint8Array {
        return this.readUint8Array(start, len);
    }

    /**
     *  <code>start</code>  <code>len</code>  <code>Uint8Array</code> 
     * @param	start	
     * @param	len		
     * @return   Uint8Array 
     */
    readUint8Array(start: number, len: number): Uint8Array {
        var end: number = start + len;
        end = (end > this._length) ? this._length : end;
        var v: any = new Uint8Array(this._d_.buffer.slice(start, end));
        this._pos_ = end;
        return v;
    }

    /**
     * @private
     * <p> <code>start</code>  <code>len</code>  <code>Int16Array</code> </p>
     * <p><b></b> Int16Array  JavaScript  HTML5 Int16Array (Int16Array.buffer) DataView  DataView </p>
     * @param	start	
     * @param	len		
     * @return   Int16Array 
     */
    getInt16Array(start: number, len: number): any {
        return this.readInt16Array(start, len);
    }

    /**
     *  <code>start</code>  <code>len</code>  <code>Int16Array</code> 
     * @param	start	
     * @param	len		
     * @return   Uint8Array 
     */
    readInt16Array(start: number, len: number): any {
        var end: number = start + len;
        end = (end > this._length) ? this._length : end;
        var v: any = new Int16Array(this._d_.buffer.slice(start, end));
        this._pos_ = end;
        return v;
    }

    /**
     * @private
     *  IEEE 754 32 
     * @return 32 
     */
    getFloat32(): number {
        return this.readFloat32();
    }

    /**
     *  IEEE 754 32 
     * @return 32 
     */
    readFloat32(): number {
        if (this._pos_ + 4 > this._length) throw "getFloat32 error - Out of bounds";
        var v: number = this._d_.getFloat32(this._pos_, this._xd_);
        this._pos_ += 4;
        return v;
    }

    /**
     * @private
     *  IEEE 754 64 
     * @return 64 
     */
    getFloat64(): number {
        return this.readFloat64();
    }

    /**
     *  IEEE 754 64 
     * @return 64 
     */
    readFloat64(): number {
        if (this._pos_ + 8 > this._length) throw "getFloat64 error - Out of bounds";
        var v: number = this._d_.getFloat64(this._pos_, this._xd_);
        this._pos_ += 8;
        return v;
    }

    /**
     *  IEEE 754 32 
     * @param	value	32 
     */
    writeFloat32(value: number): void {
        this._ensureWrite(this._pos_ + 4);
        this._d_.setFloat32(this._pos_, value, this._xd_);
        this._pos_ += 4;
    }

    /**
     *  IEEE 754 64 
     * @param	value	64 
     */
    writeFloat64(value: number): void {
        this._ensureWrite(this._pos_ + 8);
        this._d_.setFloat64(this._pos_, value, this._xd_);
        this._pos_ += 8;
    }

    /**
     * @private
     *  Int32 
     * @return Int32 
     */
    getInt32(): number {
        return this.readInt32();
    }

    /**
     *  Int32 
     * @return Int32 
     */
    readInt32(): number {
        if (this._pos_ + 4 > this._length) throw "getInt32 error - Out of bounds";
        var float: number = this._d_.getInt32(this._pos_, this._xd_);
        this._pos_ += 4;
        return float;
    }

    /**
     * @private
     *  Uint32 
     * @return Uint32 
     */
    getUint32(): number {
        return this.readUint32();
    }

    /**
     *  Uint32 
     * @return Uint32 
     */
    readUint32(): number {
        if (this._pos_ + 4 > this._length) throw "getUint32 error - Out of bounds";
        var v: number = this._d_.getUint32(this._pos_, this._xd_);
        this._pos_ += 4;
        return v;
    }

    /**
     *  Int32 
     * @param	value	 Int32 
     */
    writeInt32(value: number): void {
        this._ensureWrite(this._pos_ + 4);
        this._d_.setInt32(this._pos_, value, this._xd_);
        this._pos_ += 4;
    }

    /**
     *  Uint32 
     * @param	value	 Uint32 
     */
    writeUint32(value: number): void {
        this._ensureWrite(this._pos_ + 4);
        this._d_.setUint32(this._pos_, value, this._xd_);
        this._pos_ += 4;
    }

    /**
     * @private
     *  Int16 
     * @return Int16 
     */
    getInt16(): number {
        return this.readInt16();
    }

    /**
     *  Int16 
     * @return Int16 
     */
    readInt16(): number {
        if (this._pos_ + 2 > this._length) throw "getInt16 error - Out of bounds";
        var us: number = this._d_.getInt16(this._pos_, this._xd_);
        this._pos_ += 2;
        return us;
    }

    /**
     * @private
     *  Uint16 
     * @return Uint16 
     */
    getUint16(): number {
        return this.readUint16();
    }

    /**
     *  Uint16 
     * @return Uint16 
     */
    readUint16(): number {
        if (this._pos_ + 2 > this._length) throw "getUint16 error - Out of bounds";
        var us: number = this._d_.getUint16(this._pos_, this._xd_);
        this._pos_ += 2;
        return us;
    }

    /**
     *  Uint16 
     * @param	value	Uint16 
     */
    writeUint16(value: number): void {
        this._ensureWrite(this._pos_ + 2);
        this._d_.setUint16(this._pos_, value, this._xd_);
        this._pos_ += 2;
    }

    /**
     *  Int16 
     * @param	value	 Int16 
     */
    writeInt16(value: number): void {
        this._ensureWrite(this._pos_ + 2);
        this._d_.setInt16(this._pos_, value, this._xd_);
        this._pos_ += 2;
    }

    /**
     * @private
     *  Uint8 
     * @return Uint8 
     */
    getUint8(): number {
        return this.readUint8();
    }

    /**
     *  Uint8 
     * @return Uint8 
     */
    readUint8(): number {
        if (this._pos_ + 1 > this._length) throw "getUint8 error - Out of bounds";
        return this._u8d_[this._pos_++];
    }

    /**
     *  Uint8 
     * @param	value	 Uint8 
     */
    writeUint8(value: number): void {
        this._ensureWrite(this._pos_ + 1);
        this._d_.setUint8(this._pos_, value);
        this._pos_++;
    }

    /**
     * @internal
     *  Uint8 
     * @param	pos	
     * @return Uint8 
     */
    //TODO:coverage
    _getUInt8(pos: number): number {
        return this._readUInt8(pos);
    }

    /**
     * @internal
     *  Uint8 
     * @param	pos	
     * @return Uint8 
     */
    //TODO:coverage
    _readUInt8(pos: number): number {
        return this._d_.getUint8(pos);
    }

    /**
     * @internal
     *  Uint16 
     * @param	pos	
     * @return Uint16 
     */
    //TODO:coverage
    _getUint16(pos: number): number {
        return this._readUint16(pos);
    }

    /**
     * @internal
     *  Uint16 
     * @param	pos	
     * @return Uint16 
     */
    //TODO:coverage
    _readUint16(pos: number): number {
        return this._d_.getUint16(pos, this._xd_);
    }

    /**
     * @internal
     *  getFloat32() 6 Matrix 
     * @return  Matrix 
     */
    //TODO:coverage
    _getMatrix(): Matrix {
        return this._readMatrix();
    }

    /**
     * @internal
     *  getFloat32() 6 Matrix 
     * @return  Matrix 
     */
    //TODO:coverage
    _readMatrix(): Matrix {
        var rst: Matrix = new Matrix(this.getFloat32(), this.getFloat32(), this.getFloat32(), this.getFloat32(), this.getFloat32(), this.getFloat32());
        return rst;
    }

    /**
     * @private
     *  UTF 
     * @param	len 
     * @return 
     */
    private _rUTF(len: number): string {
        var v: string = "", max: number = this._pos_ + len, c: number, c2: number, c3: number, f: Function = String.fromCharCode;
        var u: any = this._u8d_, i: number = 0;
        var strs: any[] = [];
        var n: number = 0;
        strs.length = 1000;
        while (this._pos_ < max) {
            c = u[this._pos_++];
            if (c < 0x80) {
                if (c != 0)
                    //v += f(c);\
                    strs[n++] = f(c);
            } else if (c < 0xE0) {
                //v += f(((c & 0x3F) << 6) | (u[_pos_++] & 0x7F));
                strs[n++] = f(((c & 0x3F) << 6) | (u[this._pos_++] & 0x7F));
            } else if (c < 0xF0) {
                c2 = u[this._pos_++];
                //v += f(((c & 0x1F) << 12) | ((c2 & 0x7F) << 6) | (u[_pos_++] & 0x7F));
                strs[n++] = f(((c & 0x1F) << 12) | ((c2 & 0x7F) << 6) | (u[this._pos_++] & 0x7F));
            } else {
                c2 = u[this._pos_++];
                c3 = u[this._pos_++];
                //v += f(((c & 0x0F) << 18) | ((c2 & 0x7F) << 12) | ((c3 << 6) & 0x7F) | (u[_pos_++] & 0x7F));
                const _code = ((c & 0x0F) << 18) | ((c2 & 0x7F) << 12) | ((c3 & 0x7F) << 6) | (u[this._pos_++] & 0x7F);
                if (_code >= 0x10000) {
                    const _offset = _code - 0x10000;
                    const _lead = 0xd800 | (_offset >> 10);
                    const _trail = 0xdc00 | (_offset & 0x3ff);
                    strs[n++] = f(_lead);
                    strs[n++] = f(_trail);
                }
                else {
                    strs[n++] = f(_code);
                }
            }
            i++;
        }
        strs.length = n;
        return strs.join('');
        //return v;
    }

    /**
     * @private
     *  <code>len</code> 
     * @param	len	
     * @return 
     */
    //TODO:coverage
    getCustomString(len: number): string {
        return this.readCustomString(len);
    }

    /**
     * @private
     *  <code>len</code> 
     * @param	len	
     * @return 
     */
    //TODO:coverage
    readCustomString(len: number): string {
        var v: string = "", ulen: number = 0, c: number, c2: number, f: Function = String.fromCharCode;
        var u: any = this._u8d_, i: number = 0;
        while (len > 0) {
            c = u[this._pos_];
            if (c < 0x80) {
                v += f(c);
                this._pos_++;
                len--;
            } else {
                ulen = c - 0x80;
                this._pos_++;
                len -= ulen;
                while (ulen > 0) {
                    c = u[this._pos_++];
                    c2 = u[this._pos_++];
                    v += f((c2 << 8) | c);
                    ulen--;
                }
            }
        }

        return v;
    }

    /**
     *  Byte 
     */
    get pos(): number {
        return this._pos_;
    }

    set pos(value: number) {
        this._pos_ = value;
        //$MOD byteOffset
        //_d_.byteOffset = value;
    }

    /**
     * 
     */
    get bytesAvailable(): number {
        return this._length - this._pos_;
    }

    /**
     *  length  pos  0 Byte 
     */
    clear(): void {
        this._pos_ = 0;
        this.length = 0;
    }

    /**
     * @internal
     *  ArrayBuffer 
     * @return
     */
    __getBuffer(): ArrayBuffer {
        //this._d_.buffer.byteLength = this.length;
        return this._d_.buffer;
    }

    /**
     * <p> UTF-8  writeUTF()  writeUTFBytes()  16 </p>
     * <p> getUTFBytes </p>
     * @param value 
     */
    writeUTFBytes(value: string): void {
        // utf8-decode
        value = value + "";
        for (var i: number = 0, sz: number = value.length; i < sz; i++) {
            var c: number = value.charCodeAt(i);

            if (c <= 0x7F) {
                this.writeByte(c);
            } else if (c <= 0x7FF) {
                //writeByte
                this._ensureWrite(this._pos_ + 2);
                this._u8d_.set([0xC0 | (c >> 6), 0x80 | (c & 0x3F)], this._pos_);
                this._pos_ += 2;
            } else if (c >= 0xD800 && c <= 0xDBFF) {
                i++;
                const c2 = value.charCodeAt(i);
                if (!Number.isNaN(c2) && c2 >= 0xDC00 && c2 <= 0xDFFF) {
                    const _p1 = (c & 0x3FF) + 0x40;
                    const _p2 = c2 & 0x3FF;

                    const _b1 = 0xF0 | ((_p1 >> 8) & 0x3F);
                    const _b2 = 0x80 | ((_p1 >> 2) & 0x3F);
                    const _b3 = 0x80 | ((_p1 & 0x3) << 4) | ((_p2 >> 6) & 0xF);
                    const _b4 = 0x80 | (_p2 & 0x3F);

                    this._ensureWrite(this._pos_ + 4);
                    this._u8d_.set([_b1, _b2, _b3, _b4], this._pos_);
                    this._pos_ += 4;
                }
            } else if (c <= 0xFFFF) {
                this._ensureWrite(this._pos_ + 3);
                this._u8d_.set([0xE0 | (c >> 12), 0x80 | ((c >> 6) & 0x3F), 0x80 | (c & 0x3F)], this._pos_);
                this._pos_ += 3;
            } else {
                this._ensureWrite(this._pos_ + 4);
                this._u8d_.set([0xF0 | (c >> 18), 0x80 | ((c >> 12) & 0x3F), 0x80 | ((c >> 6) & 0x3F), 0x80 | (c & 0x3F)], this._pos_);
                this._pos_ += 4;
            }
        }
    }

    /**
     * <p> UTF-8  UTF-8  16 </p>
     * <p> getUTFString </p>
     * @param	value 
     */
    writeUTFString(value: string): void {
        var tPos: number = this.pos;
        this.writeUint16(1);
        this.writeUTFBytes(value);
        var dPos: number = this.pos - tPos - 2;
        //trace("writeLen:",dPos,"pos:",tPos);
        this._d_.setUint16(tPos, dPos, this._xd_);
    }

    /**
     * <p> UTF-8  UTF-8  32 </p>
     * @param	value 
     */
    writeUTFString32(value: string): void {
        var tPos = this.pos;
        this.writeUint32(1);
        this.writeUTFBytes(value);
        var dPos = this.pos - tPos - 4;
        //trace("writeLen:",dPos,"pos:",tPos);
        this._d_.setUint32(tPos, dPos, this._xd_);
    }


    /**
     * @private
     *  UTF-8 
     * @return 
     */
    readUTFString(): string {
        //var tPos:int = pos;
        //var len:int = getUint16();
        ////trace("readLen:"+len,"pos,",tPos);
        return this.readUTFBytes(this.getUint16());
    }

    /**
     * @private
     */
    readUTFString32(): string {
        return this.readUTFBytes(this.getUint32());
    }

    /**
     * <p> UTF-8 </p>
     * <p> writeUTFString </p>
     * @return 
     */
    getUTFString(): string {
        return this.readUTFString();
    }

    /**
     * @private
     *  writeUTFBytes 
     * @param len	buffer
     * @return 
     */
    readUTFBytes(len: number = -1): string {
        if (len === 0) return "";
        var lastBytes: number = this.bytesAvailable;
        if (len > lastBytes) throw "readUTFBytes error - Out of bounds";
        len = len > 0 ? len : lastBytes;
        return this._rUTF(len);
    }

    /**
     * <p> length  UTF-8 </p>
     * <p> writeUTFBytes </p>
     * @param len	buffer
     * @return 
     */
    getUTFBytes(len: number = -1): string {
        return this.readUTFBytes(len);
    }

    /**
     * <p></p>
     * <p> 8  24 </p>
     * @param	value
     */
    writeByte(value: number): void {
        this._ensureWrite(this._pos_ + 1);
        this._d_.setInt8(this._pos_, value);
        this._pos_ += 1;
    }

    /**
     * <p></p>
     * <p> -128  127</p>
     * @return  -128  127 
     */
    readByte(): number {
        if (this._pos_ + 1 > this._length) throw "readByte error - Out of bounds";
        return this._d_.getInt8(this._pos_++);
    }

    /**
     * @private
     * 
     */
    getByte(): number {
        return this.readByte();
    }

    /**
     * @internal
     * <p> <code>lengthToEnsure</code> </p>
     * @param	lengthToEnsure	
     */
    _ensureWrite(lengthToEnsure: number): void {
        if (this._length < lengthToEnsure) this._length = lengthToEnsure;
        if (this._allocated_ < lengthToEnsure) this.length = lengthToEnsure;
    }

    /**
     * <p> arraybuffer  offset  length </p>
     * <p> length  0 offset  offset </p>
     * <p> offset  length 0</p>
     * @param	arraybuffer	 Arraybuffer 
     * @param	offset		Arraybuffer 
     * @param	length		 Arraybuffer  Byte 
     */
    writeArrayBuffer(arraybuffer: any, offset: number = 0, length: number = 0): void {
        if (offset < 0 || length < 0) throw "writeArrayBuffer error - Out of bounds";
        if (length == 0) length = arraybuffer.byteLength - offset;
        this._ensureWrite(this._pos_ + length);
        var uint8array: any = new Uint8Array(arraybuffer);
        this._u8d_.set(uint8array.subarray(offset, offset + length), this._pos_);
        this._pos_ += length;
    }

    /**
     * ArrayBuffer
     * @param	length
     * @return
     */
    readArrayBuffer(length: number): ArrayBuffer {
        var rst: ArrayBuffer;
        rst = this._u8d_.buffer.slice(this._pos_, this._pos_ + length);
        this._pos_ = this._pos_ + length
        return rst;
    }
}

