import { ClassUtils } from "./laya/utils/ClassUtils";

export type FEnumDescriptor = {
    name: string,
    value: any,
    extend?: FEnumDescriptor,
    [index: string]: any,
}[] | any[] | Object | string;

export type FPropertyType = string | Function | Object | [FPropertyType] | ["Record", FPropertyType];

export interface FPropertyDescriptor {
    /**  */
    name: string;
    /** 
     * 
     * number,string,boolean,any
     * ["number"]["Record", "number"]Record
     * regClassregClass
     * typeui
     */
    type: FPropertyType;

    /** */
    default?: any;

    /** name */
    caption: string;
    /**  */
    tips: string;

    /** Inspector */
    catalog: string;
    /*  */
    catalogCaption: string;
    /* */
    catalogOrder: number;

    /**
     * number,string,boolean,color,vec2,vec3,vec4,asset
     * 
     *      number : 
     *      string : multiline
     *      boolean : 
     *      color : ++
     *      vec2 : XY
     *      vec3 : XYZ
     *      vec4 : XYZW
     *      asset : 
     * 
     * 
     * Vector4Vector4color
     * 
     * inspectornullinspectorhiddentruehiddentrue
     * inspectornull
     */
    inspector: string;

    /** :truefalse
     * 1. "!data.a && !data.b"abtrue
     * 2. datadata.adata.babab
     */
    hidden: boolean | string;

    /** 
     * 1. "!data.a && !data.b"abtrue
     * 2. datadata.adata.babab
     */
    readonly: boolean | string;

    /** 
     * 1. 
     * 2. "if(value == data.a) return 'a'"
     * valuedatadata.aa
     */
    validator: string;
    /** 
     * truefalse
     */
    required: boolean | string;

    /**  */
    serializable: boolean;
    /** */
    affectBy: string;

    /**  */
    multiline: boolean;
    /**  */
    password: boolean;
    /** true */
    submitOnTyping: boolean;
    /**  */
    prompt: string;

    /**  */
    enumSource: FEnumDescriptor;
    /**  */
    hideIfEnumSourceEmpty: boolean;

    /** true */
    reverseBool: boolean;

    /** nulltrue*/
    nullable: boolean;

    /**  */
    min: number,
    /**  */
    max: number,
    /** minmax */
    range: [number, number];
    /**  */
    step: number;
    /**  */
    fractionDigits: number;
    /**  */
    percentage: boolean;

    /** */
    fixedLength: boolean;
    /** */
    arrayActions: Array<"append" | "insert" | "delete" | "move">;
    /**  */
    elementProps: Partial<FPropertyDescriptor>;

    /** a */
    showAlpha: boolean;
    /** defaultdefaultnulldefaultColornull*/
    defaultColor: any;
    /** checkboxnull */
    colorNullable: boolean;

    /** true*/
    hideHeader: boolean;
    /** null*/
    createObjectMenu: Array<string>;
    /** 
     * objba1a1TTstructLiketruea1obj.b = a1
     * false
     */
    structLike: boolean;

    /**  */
    isAsset: boolean;
    /** Image,Audio"*/
    assetTypeFilter: string;
    /** stringres://uuidtruefalse*/
    useAssetPath: boolean;
    /**  */
    allowInternalAssets: boolean;
    /** EditorEnv.assetMgr.customAssetFilters */
    customAssetFilter: string;

    /** NodeComponentnull*/
    toTemplate: string;

    /** before xxx/after xxx/first/last */
    position: string;

    /** Inspector */
    "private": boolean;

    /**  */
    addIndent: number;

    /** true */
    allowMultipleObjects: boolean;

    /**  */
    hideInDeriveType: boolean;

    /** 
     * func(key?:string)key
     * key 
     */
    onChange: string;

    /**  */
    options: Record<string, any>;
}

export interface FTypeDescriptor {
    /** name */
    caption: string;
    /**url */
    help: string;
    /**  */
    menu: string;
    /** */
    icon: string;
    /**  */
    isAsset: boolean;
    /** Image,Audio"editor/public/IAssetInfo.ts */
    assetTypeFilter: string;
    /** 
     * objba1a1TTstructLiketruea1obj.b = a1
     * false
     */
    structLike: boolean;
    /** */
    init: any;
    /**  */
    properties: Array<Partial<FPropertyDescriptor>>;
    /**  */
    inspector: string;
    /** Componentnull */
    worldType: "2d" | "3d" | null;
    /** Componenttruemenu */
    inHierarchyMenu: boolean;
    /**  */
    options: Record<string, any>;
}

function dummy() { }

/**
 * 
 */
export function regClass(assetId?: string): any {
    return function (constructor: Function) {
        ClassUtils.regClass(assetId, constructor);
    };
}

/**
 * 
 * @param info 
 */
export function classInfo(info?: Partial<FTypeDescriptor>): any { return dummy; }

/**
 * 
 */
export function runInEditor(constructor: Function): void { }

/**
 * 
 * @param info : Number,"number",[Number],["Record", Number]{ type: "string", multiline: true }
 */
export function property(info: FPropertyType | Partial<FPropertyDescriptor>): any { return dummy; }
