import { AnimationBase } from "./AnimationBase";
import { Graphics } from "./Graphics";
import { Loader } from "../net/Loader"
import { GraphicAnimation } from "./GraphicAnimation"
import { Handler } from "../utils/Handler"
import { Utils } from "../utils/Utils"
import { ILaya } from "../../ILaya";
import { AtlasResource } from "../resource/AtlasResource";

/**
 * 
 * @eventType Event.COMPLETE
 */
/*[Event(name = "complete", type = "laya.events.Event")]*/

/**
 * 
 * @eventType Event.LABEL
 */
/*[Event(name = "label", type = "laya.events.Event")]*/

/**
 * <p> <code>Animation</code> GraphicsGraphics</p>
 * <p>CPU</p>
 * <p>key-valuekeyvalueGraphicsGraphicsGraphics</p>
 * <p>set sourceloadImages(...)loadAtlas(...)loadAnimation(...)play(...)</p>
 * @example <caption> <code>Text</code> </caption>
 * package
 * {
 * 	import laya.display.Animation;
 * 	import laya.net.Loader;
 * 	import laya.utils.Handler;
 * 	public class Animation_Example
 * 	{
 * 		public function Animation_Example()
 * 		{
 * 			Laya.init(640, 800);//
 * 			Laya.stage.bgColor = "#efefef";//
 * 			init();//
 * 		}
 * 		private function init():void
 * 		{
 * 			var animation:Animation = new Animation();// Animation  animation 
 * 			animation.loadAtlas("resource/ani/fighter.json");//
 * 			animation.x = 200;// animation  x  animation 
 * 			animation.y = 200;// animation  x  animation 
 * 			animation.interval = 50;// animation 
 * 			animation.play();//
 * 			Laya.stage.addChild(animation);// animation 
 * 		}
 * 	}
 * }
 *
 * @example
 * Animation_Example();
 * function Animation_Example(){
 *     Laya.init(640, 800);//
 *     Laya.stage.bgColor = "#efefef";//
 *     init();//
 * }
 * function init()
 * {
 *     var animation = new Laya.Animation();// Animation  animation 
 *     animation.loadAtlas("resource/ani/fighter.json");//
 *     animation.x = 200;// animation  x  animation 
 *     animation.y = 200;// animation  x  animation 
 *     animation.interval = 50;// animation 
 *     animation.play();//
 *     Laya.stage.addChild(animation);// animation 
 * }
 *
 * @example
 * import Animation = laya.display.Animation;
 * class Animation_Example {
 *     constructor() {
 *         Laya.init(640, 800);//
 *         Laya.stage.bgColor = "#efefef";//
 *         this.init();
 *     }
 *     private init(): void {
 *         var animation:Animation = new Laya.Animation();// Animation  animation 
 *         animation.loadAtlas("resource/ani/fighter.json");//
 *         animation.x = 200;// animation  x  animation 
 *         animation.y = 200;// animation  x  animation 
 *         animation.interval = 50;// animation 
 *         animation.play();//
 *         Laya.stage.addChild(animation);// animation 
 *     }
 * }
 * new Animation_Example();
 */
export class Animation extends AnimationBase {
    /**
     * <p>key-valuekeyvalueGraphicsGraphicsGraphics</p>
     * <p>loadImages(...)loadAtlas(...)loadAnimation(...)set sourceplay(...)</p>
     */
    static framesMap: any = {};
    /**@private */
    protected _frames: any[];

    private _source: string;

    private _autoPlay = false;


    /**
     *  <code>Animation</code> 
     */
    constructor() {
        super();
        this._setControlNode(this);
    }

    /** @inheritDoc
     *  @override
     */
    destroy(destroyChild: boolean = true): void {
        this.stop();
        super.destroy(destroyChild);
        this._frames = null;
        this._labels = null;
    }

    /**
     * <p>keyname </p>
     * <p>play(...)(set frames)</p>
     * <p>wrapMode</p>
     * @param	start	(int)(String)addLabel(...)removeLabel(...)
     * @param	loop	
     * @param	name	keynamekeyname
     * @override
     */
    play(start: any = 0, loop: boolean = true, name: string = ""): void {
        if (name) this._setFramesFromCache(name, true);
        super.play(start, loop, name);
    }

    /**@private */
    protected _setFramesFromCache(name: string, showWarn: boolean = false): boolean {
        if (this._url) name = this._url + "#" + name;
        if (name && Animation.framesMap[name]) {
            var tAniO: any = Animation.framesMap[name];
            if (tAniO instanceof Array) {
                this._frames = Animation.framesMap[name];
                this._count = this._frames.length;
            } else {
                if (tAniO.nodeRoot) {
                    //,
                    Animation.framesMap[name] = GraphicAnimation.parseAnimationByData(tAniO);
                    tAniO = Animation.framesMap[name];
                }
                this._frames = tAniO.frames;
                this._count = this._frames.length;
                //
                if (!this._frameRateChanged) this._interval = tAniO.interval;
                this._labels = this._copyLabels(tAniO.labels);
            }
            return true;
        } else {
            if (showWarn) console.log("ani not found:", name);
        }
        return false;
    }

    /**@private */
    private _copyLabels(labels: any): any {
        if (!labels) return null;
        var rst: any;
        rst = {};
        var key: string;
        for (key in labels) {
            rst[key] = Utils.copyArray([], labels[key]);
        }
        return rst;
    }

    /**@private 
    *  @override
    */
    protected _frameLoop(): void {
        if (this._visible && this._style.alpha > 0.01 && this._frames) {
            super._frameLoop();
        }
    }

    /**@private 
     * @override
    */
    protected _displayToIndex(value: number): void {
        if (this._frames) this.graphics = this._frames[value];
    }

    /**
     * GraphicsGraphics
     */
    get frames(): any[] {
        return this._frames;
    }

    set frames(value: any[]) {
        this._frames = value;
        if (value) {
            this._count = value.length;
            //if (_isPlaying) play(_index, loop, _actionName);
            if (this._actionName) this._setFramesFromCache(this._actionName, true);
            this.index = this._index;
        }
    }

    /**
     * <p></p>
     * <p><br/>
     * 1. LayaAir IDEloadAnimation(...)<br/>
     * 2. loadAtlas(...)<br/>
     * 3. loadImages(...)</p>
     * @param value	"xx/a1.atlas""a1.png,a2.png,a3.png"LayaAir IDE"xx/a1.ani"
     */
    get source(): string {
        return this._source;
    }

    set source(value: string) {
        this._source = value;

        if (!value)
            this.clear();
        else if (value.indexOf(".ani") > -1)
            this.loadAnimation(value);
        else if (value.startsWith("res://") || value.indexOf(".json") > -1 || value.indexOf("als") > -1 || value.indexOf("atlas") > -1)
            this.loadAtlas(value);
        else
            this.loadImages(value.split(","));
    }

    /**
     * falsetrue
     */
    set autoPlay(value: boolean) {
        this._autoPlay = value;
        if (value)
            this.play();
        else
            this.stop();
    }

    get autoPlay() {
        return this._autoPlay;
    }

    /**
     * 
     * @override
     */
    clear(): AnimationBase {
        super.clear();
        this.stop();
        this.graphics = null;
        this._frames = null;
        this._labels = null;
        return this;
    }

    /**
     * <p>1. keycacheName2. cacheNamekey</p>
     * <p>CPU</p>
     * <p>AnimationloadImages(...).loadImages(...).play(...);</p>
     * @param	urls		[url1,url2,url3,...]
     * @param	cacheName	keykeycacheNamecacheNamecacheNamekeycacheName
     * @return 	Animation
     */
    loadImages(urls: any[], cacheName: string = ""): Animation {
        this._url = "";
        if (!this._setFramesFromCache(cacheName)) {
            this.frames = Animation.framesMap[cacheName] ? Animation.framesMap[cacheName] : Animation.createFrames(urls, cacheName);
        }
        if (!this._isPlaying && this._autoPlay)
            this.play();
        return this;
    }

    /**
     * <p>1. keycacheName2. </p>
     * <p>cacheNamekey</p>
     * <p>CPU</p>
     * <p>AnimationloadAtlas(...).loadAtlas(...).play(...);</p>
     * @param	url			
     * @param	loaded		
     * @param	cacheName	keykeycacheNamecacheNamecacheNamekeycacheName
     * @return 	
     */
    loadAtlas(url: string, loaded: Handler = null, cacheName: string = ""): Animation {
        this._url = "";
        if (!this._setFramesFromCache(cacheName)) {
            let onLoaded = (loadUrl: string) => {
                if (url === loadUrl) {
                    this.frames = Animation.framesMap[cacheName] ? Animation.framesMap[cacheName] : Animation.createFrames(url, cacheName);
                    if (!this._isPlaying && this._autoPlay)
                        this.play();
                    if (loaded) loaded.run();
                }
            }
            if (Loader.getAtlas(url)) onLoaded(url);
            else ILaya.loader.load(url, Handler.create(null, onLoaded, [url]), null, Loader.ATLAS);
        }
        return this;
    }

    /**
     * <p>LayaAir IDEIDEset intervalkey "url#" key "url#" </p>
     * <p>atlas</p>
     * <p>CPU</p>
     * <p>AnimationloadAnimation(...).loadAnimation(...).play(...);</p>
     * @param	url 	LayaAir IDE
     * @param	loaded	
     * @param	atlas	
     * @return 	
     * @deprecated
     */
    loadAnimation(url: string, loaded: Handler = null, atlas: string = null): Animation {
        this._url = url;
        var _this: Animation = this;
        if (!this._actionName) this._actionName = "";
        if (!_this._setFramesFromCache(this._actionName)) {
            if (!atlas || Loader.getAtlas(atlas)) {
                this._loadAnimationData(url, loaded, atlas);
            } else {
                ILaya.loader.load(atlas, Handler.create(this, this._loadAnimationData, [url, loaded, atlas]), null, Loader.ATLAS)
            }
        } else {
            _this._setFramesFromCache(this._actionName, true);
            this.index = 0;
            if (loaded) loaded.run();
        }
        return this;
    }

    /**@private */
    private _loadAnimationData(url: string, loaded: Handler = null, atlas: string = null): void {
        if (atlas && !Loader.getAtlas(atlas)) {
            console.warn("atlas load fail:" + atlas);
            return;
        }

        ILaya.loader.fetch(url, "json").then(data => {
            if (this._url !== url)
                return;

            if (!data) {
                // getRes
                // frameMap--
                if (Animation.framesMap[url + "#"]) {
                    this._setFramesFromCache(this._actionName, true);
                    this.index = 0;
                    this._resumePlay();
                    if (loaded) loaded.run();
                }
                return;
            }

            let tAniO: any;
            if (!Animation.framesMap[url + "#"]) {
                //graphic
                let aniData: any = GraphicAnimation.parseAnimationData(data);
                if (!aniData) return;
                //
                let aniList: any[] = aniData.animationList;
                let len: number = aniList.length;
                let defaultO: any;
                for (let i = 0; i < len; i++) {
                    tAniO = aniList[i];
                    Animation.framesMap[url + "#" + tAniO.name] = tAniO;
                    if (!defaultO) defaultO = tAniO;
                }
                if (defaultO) {
                    Animation.framesMap[url + "#"] = defaultO;
                    this._setFramesFromCache(this._actionName, true);
                    this.index = 0;
                }
                this._resumePlay();
            } else {
                this._setFramesFromCache(this._actionName, true);
                this.index = 0;
                this._resumePlay();
            }
            if (loaded) loaded.run();
        });
    }

    /**
     * <p>Graphics</p>
     * @param	url			
     * @param	name		keykey
     * @return	
     */
    static createFrames(url: string | string[], name: string): any[] {
        var arr: any[];
        if (typeof (url) == 'string') {
            var atlas: AtlasResource = Loader.getAtlas(<string>url);
            if (atlas && atlas.frames.length) {
                let frames = atlas.frames;
                arr = [];
                for (var i: number = 0, n: number = frames.length; i < n; i++) {
                    var g: Graphics = new Graphics();
                    g.drawImage(frames[i], 0, 0);
                    arr.push(g);
                }
            }
        } else if (url instanceof Array) {
            arr = [];
            for (i = 0, n = url.length; i < n; i++) {
                g = new Graphics();
                g.loadImage(url[i], 0, 0);
                arr.push(g);
            }
        }
        if (name) Animation.framesMap[name] = arr;
        return arr;
    }

    /**
     * <p>key</p>
     * <p>LayaAir IDEkey"url#""url#aniName"</p>
     * @param key key
     */
    static clearCache(key: string): void {
        var cache: any = Animation.framesMap;
        var val: string;
        var key2: string = key + "#";
        for (val in cache) {
            if (val === key || val.indexOf(key2) === 0) {
                delete Animation.framesMap[val];
            }
        }
    }

    /** @internal */
    onAfterDeserialize(): void {
        super.onAfterDeserialize();

        if ((<any>this).images) {
            if (!this._source)
                this.loadImages((<any>this).images);
            delete (<any>this).images;
        }
    }
}

