import { Pool } from "../utils/Pool"

/**
 * <code>Point</code>  x y 
 */
export class Point {

    /***/
    static TEMP: Point = new Point();
    /**@private point(x=0y=0)*/
    static EMPTY: Point = new Point();

    /***/
    x: number;
    /***/
    y: number;

    /**
     *  <code>Point</code> 
     * @param x	
     * @param y	
     */
    constructor(x: number = 0, y: number = 0) {
        this.x = x;
        this.y = y;
    }

    /**
     * 
     */
    static create(): Point {
        return Pool.getItemByClass("Point", Point);
    }

    /**
     *  <code>Point</code> 
     * @param	x 
     * @param	y 
     * @return  Point 
     */
    setTo(x: number, y: number): Point {
        this.x = x;
        this.y = y;
        return this;
    }

    /**
     * 
     */
    reset(): Point {
        this.x = this.y = 0;
        return this;
    }

    /**
     * 
     */
    recover(): void {
        Pool.recover("Point", this.reset());
    }

    /**
     * (xy)
     * @param	x 
     * @param	y 
     * @return	
     */
    distance(x: number, y: number): number {
        return Math.sqrt((this.x - x) * (this.x - x) + (this.y - y) * (this.y - y));
    }

    /** x  y */
    toString(): string {
        return this.x + "," + this.y;
    }

    /**
     * 
     */
    normalize(): void {
        var d: number = Math.sqrt(this.x * this.x + this.y * this.y);
        if (d > 0) {
            var id: number = 1.0 / d;
            this.x *= id;
            this.y *= id;
        }
    }

    /**
     * copy point
     * @param	point copypoint
     */
    copy(point: Point): Point {
        return this.setTo(point.x, point.y);
    }
}

