import { ILaya } from "../../ILaya";
import { LayaEnv } from "../../LayaEnv";
import { NodeFlags } from "../Const";
import { Node } from "../display/Node"
import { Pool } from "../utils/Pool"
import { Utils } from "../utils/Utils";

/**
 * <code>Component</code> 
 */
export class Component {
    /** @private */
    _id: number;
    /**@private */
    private _hideFlags: number = 0;
    /**@private */
    private _enableState: boolean;
    /** @internal */
    _status: number = 0; //1-awaked,2-starting,3-started,4-destroyed

    /**
     * Node
     */
    owner: Node;
    /** @internal */
    _enabled: boolean = true;
    /**
     * 
     */
    _singleton?: boolean = true;
    /**
     * IDE
     */
    runInEditor: boolean;
    scriptPath: string;
    _extra: IComponentExtra;

    get hideFlags(): number {
        return this._hideFlags;
    }

    set hideFlags(value: number) {
        this._hideFlags = value;
    }

    /**
     *  <code>Component</code> 
     */
    constructor() {
        this._id = Utils.getGID();

        this._initialize();
    }

    //@internal
    _initialize(): void {
        this._extra = {};
    }

    hasHideFlag(flag: number): boolean {
        return (this._hideFlags & flag) != 0;
    }

    /**
     * ID
     */
    get id(): number {
        return this._id;
    }

    /**
     * 
     */
    get enabled(): boolean {
        return this._enabled;
    }

    set enabled(value: boolean) {
        if (this._enabled != value) {
            this._enabled = value;
            if (this.owner)
                this._setActive(value && this.owner.activeInHierarchy);
        }
    }

    get awaked(): boolean {
        return this._status > 0;
    }

    /**
     *  
     */
    get destroyed(): boolean {
        return this._status == 4;
    }
    /**
     * @internal
     */
    _isScript(): boolean {
        return false;
    }
    /**
     * @internal
     */
    protected _resetComp(): void {
        this._enabled = true;
        this._status = 0;
        this._enableState = false;
        this.owner = null;
    }

    _setOwner(node: Node) {
        if (this._status != 0) {
            throw 'reuse a destroyed component';
        }
        this.owner = node;

        if (this._isScript())
            node._setBit(NodeFlags.HAS_SCRIPT, true);

        this._onAdded();
        this.onAdded();
    }

    /**
     * 
     * @internal
     */
    protected _onAdded(): void {
    }

    /**
   * 
   * @internal
   */
    protected _onAwake(): void {
    }

    /**
     * 
     * @internal
     */
    protected _onEnable(): void {
        this.onEnable();
    }

    /**
    * 
    * @internal
    * 
    */
    protected _onDisable(): void {
        this.onDisable();
    }

    /**
     * 
     * @internal
     */
    protected _onDestroy(): void {
    }

    /**
     * @internal
     */
    _parse(data: any, interactMap: any = null): void {
        //override it.
    }

    /**
     * @internal
     */
    _parseInteractive(data: any = null, spriteMap: any = null) {
        //override it.
    }

    /**
     * @internal
     */
    _cloneTo(dest: Component): void {
        //override it.
    }

    /**
     * @internal
     */
    _setActive(value: boolean): void {
        if (value) {
            if (this._status == 0) {
                this._status = 1;

                if (LayaEnv.isPlaying || this.runInEditor) {
                    this._onAwake();
                    this.onAwake();
                }
            }
            if (this._enabled && !this._enableState) {
                this._enableState = true;

                if (LayaEnv.isPlaying || this.runInEditor) {
                    let driver = (this.owner._is3D && this.owner._scene)?._componentDriver || ILaya.stage._componentDriver;
                    driver.add(this);

                    if (LayaEnv.isPlaying && this._isScript())
                        this.setupScript();

                    this._onEnable();
                }
            }
        } else if (this._enableState) {
            this._enableState = false;
            if (LayaEnv.isPlaying || this.runInEditor) {
                let driver = (this.owner._is3D && this.owner._scene)?._componentDriver || ILaya.stage._componentDriver;
                driver.remove(this);

                ILaya.stage.offAllCaller(this);

                this._onDisable();
            }
        }
    }

    protected setupScript() {
    }

    /**
     * 
     */
    destroy(): void {
        if (this._status == 4)
            return;

        if (this.owner)
            this.owner._destroyComponent(this);
        else if (!this.destroyed)
            this._destroy(true);
    }

    /**
     * @internal
     */
    _destroy(second?: boolean): void {
        if (second) {
            if (LayaEnv.isPlaying || this.runInEditor) {
                this._onDestroy();
                this.onDestroy();

                if (this.onReset) {
                    this.onReset();
                    this._resetComp();
                    Pool.recoverByClass(this);
                }
            }
            return;
        }

        this._setActive(false);
        this._status = 4;

        if (LayaEnv.isPlaying || this.runInEditor) {
            let driver = (this.owner._is3D && this.owner._scene)?._componentDriver || ILaya.stage._componentDriver;
            driver._toDestroys.add(this);
        }
    }

    /**
     * AwakeonAdded
     */
    onAdded(): void {
    }

    /**
     * 
     * 

     */
    onReset?(): void;

    /**
     * 
     */
    onAwake(): void {
    }

    /**
     * 
     */
    onEnable(): void {
    }

    /**
     * update
     */
    onStart?(): void;

    /**
     * getComponent
     */
    onUpdate?(): void;

    /**
     * updategetComponent
     */
    onLateUpdate?(): void;

    /**
     * 
     */
    onPreRender?(): void;

    /**
     * 
     */
    onPostRender?(): void;

    /**
     * 
     */
    onDisable(): void {
    }

    /**
     * 
     */
    onDestroy(): void {
    }
}

export interface IComponentExtra { }